$border-color: #f2f2f2;

/* Tab Navigation */
.tabs__nav {
  border-bottom: none;
  @include list-reset;

  li {
    margin-bottom: 0;
    margin-right: .25rem;
    //border-radius: 3px 0 0 0;
    overflow: visible;
  }

  @include large {
    border-left: none;
    display: flex;
  }
}
.bg-gr,
.bg-gr-wave,
.bg-gr-wave-2,
.bg-ye,
.filter-darken {
  .tabs {
    p { color: black; }
  }
  .tabs__link {
    background-color: #fafafa;
    color: #737373;
    border-top: .25rem solid #fafafa;
    border-right: 1px solid #fafafa;
    border-left: 1px solid #fafafa;
    &:hover {
      background: #fafafa;
      color: black;
      border-top: .25rem solid $border-color;
      border-right: 1px solid $border-color;
      border-left: 1px solid $border-color;
      border-bottom: 1px solid $border-color;
    }
  }
}

.bg-ye {
  .tabs__link {
    &.is-active {
      border-top: .25rem solid $oswego-green;
    }
  }
}

.tabs__link {
  border-radius: 3px 3px 0 0;
  border: none;
  background-color: #fafafa;
  color: #737373;
  border-top: .25rem solid #fafafa;
  border-right: 1px solid #fafafa;
  border-left: 1px solid #fafafa;
  border-bottom: 1px solid $border-color;
  transition: all .25s;
  &:hover {
    background: #fafafa;
    color: black;
    border-top: .25rem solid $border-color;
    border-right: 1px solid $border-color;
    border-left: 1px solid $border-color;
    border-bottom: 1px solid $border-color;
    transition: all .5s;
  }
  &.is-active {
    border-bottom: 1px solid white;
    background: #fff;
    border-top: .25rem solid $oswego-gold;
    border-left: 1px solid $border-color;
    border-right: 1px solid $border-color;
    color: black;
    transition: all .5s;
    &:hover {
      background: #fff;
    }
  }
}

/* Tab Content (hidden only in full #tabs js version) */
.tabs__tab {
  display: none;
  border: 1px solid $border-color;
  background-color: #fff;
  border-radius: 0 3px 3px 3px;

  &.is-active {
    display: block;
  }
}

.tabs__content {
  padding: 1rem;
}

/* No-js fallback */
.tabs.no-js {
  .tabs__tab.is-active {
    display: block;
  }
}

/* Drupal Local Tasks variant */
.tabs__nav--local-tasks {
  margin: 1em 0 0.5em;
  padding: 0;
}

.tabs__link--local-tasks {
  padding: .5rem 1rem;
  border-left: 1px solid $border-color;
  &.is-active {
    border-left: 1px solid $border-color;
  }
}
