#site-login {
  text-decoration: none;
}
.footer {
  //margin-top: 4rem;
  padding: 4rem 1rem;
  background-color: $gray-darker;
  color: white;
  #cta-links {
    margin-bottom: 2rem;
    .icon {
      width: 1.5em;
      height: 1.5em;
      margin-right: .5rem;
    }
    .button {
      margin: 0;
      display: flex;
      align-items: center;
      justify-content: center;

      @include drop;
      @include rounded;

      &:hover {
        background-color: darken(#2f2f2f, 2%);
        border-color: $oswego-gold;
      }
    }
  }
  &__container {

    @media screen and (min-width: $medium) {
      display: flex;
    }
  }

  hr {
    border: 0;
    height: 1px;
    background: #484746;
    &.columns-line {
      display: none;
      position: absolute;
      left: 0;
      right: 0;
      top: 2.75rem;
      @media screen and (min-width: $medium) {
        display: block;
      }
    }
  }

  &__links-container {
    padding-top: $space4;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    @media screen and (min-width: $medium) {
      flex-wrap: nowrap;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-gap: 2rem;
    }
  }

  &__links-column {
    width: 100%;
    @media screen and (min-width: $medium) {
      padding-left: 1rem;
    }

  }

  &__links_heading {
    color: #45b17d;
    margin-top: 2rem;
    @media screen and (min-width: $medium) {
      margin-top: 0;
      margin-bottom: 1.5rem
    }
  }
  &__links {
    padding: 0;

  }
  a {
    color: $near-white;
    transition: color .5s;
    &:hover {
      color: $oswego-gold;
      transition: color .25s;
    }
  }

  .social-icons {
    a {
      text-decoration: none;
    }
  }
  li {
    line-height: 1.5;
  }
  .footer__address-container {
    display: none;
  }
  .footer__address-container--mobile {
    margin-top: 4rem;
    display: block;
  }
  @media screen and (min-width: $medium) {
    .footer__address-container {
      display: block;
    }
    .footer__address-container--mobile {
      display: none;
    }
  }

}
